  .wrapper__main {
    display: flex;
  }
  .image__close {
    display: none;
  }
}
.wrapper__sidebar {
  display: block;
  background-color: #ffffff;
}
@media (max-width: 1439px) {
  .wrapper__sidebar {
    display: none;
  }
}

@media (max-width: 1439px) {
  .open {
    display: block;
    position: absolute;
    height: 100%;
    z-index: 1000;
  }
  .btn {
    display: none;
  }
}
.sidebar__menu {
  display: flex;
  height: 88px;
  margin: 0;
  padding: 0;
  background-color: #ffffff;
  width: 320px;
  box-shadow: 2px 0 52px rgba(14, 24, 80, 0.2);
}
.sidebar__image {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
}

.image__burger {
  margin-left: 24px;
  margin-top: 23px;
}

.image__group {
  margin-left: 16px;
  margin-top: 18px;
}

.image__search {
  margin-left: 78px;
  margin-top: 23px;
}

/*-------------MAIN---------------*/
.sidebar-list {
  margin: 0;
  padding: 0;
}
.sidebar-list__button {
  list-style: none;
}
.sidebar-list__button:hover {
  background-color: #ff3e79;
  transition: 1s;
}
.sidebar-list__highlight {
  width: 4px;
  height: 32px;
  display: flex;
}
.sidebar-list__link {
  display: block;
  text-decoration: none;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: -0.2px;
  color: #1b1c21;
  margin-top: 32px;
  margin-left: 24px;
}
.sidebar-list__link_grey {
  margin-left: 20px;
  color: #7e7e82;
  margin-top: 4px;
}

.title {
  display: flex;
  margin-top: 28px;
}

/*----------SDECTION----------*/
.contacts {
  width: 196px;
  height: 124px;
  margin-left: 24px;
  margin-top: 268px;
}
.contacts__email {
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: -0.2px;
  color: #1b1c21;
  text-decoration: none;
  display: block;
  margin-top: 12px;
}
.contacts__number {
  text-decoration: none;
  display: block;
  font-weight: 700;
  font-size: 24px;
  line-height: 32px;
  letter-spacing: -0.6px;
  color: #1b1c21;
  margin-top: 16px;
}

.contacts-icon {
  list-style: none;
  display: flex;
  margin: 0;
  padding: 0;
}
.contacts-icon__chat {
  margin-left: 16px;
}
.contacts-icon__profile {
  margin-left: 16px;
}

/*-----------------------footer--------------*/
.lang {
  display: flex;
  margin-left: 24px;
  margin-top: 40px;
}
.lang__ru {
  color: #7e7e82;
}
.lang__en {
  color: #1b1c21;
}
.lang__ch {
  color: #1b1c21;
}

.button {
  text-decoration: none;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: -0.2px;
