___CSS_LOADER_EXPORT___.push([module.id, `.modal-call {
  position: fixed;
  top: 0;
  left: 70%;
  width: 30%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
  justify-content: flex-start;
  z-index: 1000;
  display: none;
  background-color: #ffffff;
}
@media (max-width: 600px) {
  .modal-call {
    left: 0;
    width: 100%;
  }
}
.modal-call__button {
  padding-top: 24px;
  padding-left: 16px;
}

.active {
  display: flex;
}

.header-modal-call {
  margin-top: 30px;
}
.header-modal-call__title {
  display: flex;
}
.header-modal-call__name {
  padding-top: 10px;
  padding-left: 12px;
  font-weight: 700;
  font-size: 24px;
  line-height: 32px;
  letter-spacing: -0.6px;
  color: #1b1c21;
}

.main-modal-call {
  margin-top: 24px;
}
.main-modal-call__text {
  margin-left: 16px;
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;
  color: #7e7e82;
}

.number__input {
  border: 1px solid #7e7e82;
  border-radius: 8px;
  height: 48px;
  width: 80%;
  margin-left: 16px;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: -0.2px;
  padding-left: 32px;
}

.pink {
  color: #ff3e79;
}

.footer-send {
  margin-top: 18px;
}
.footer-send__button {
  display: flex;
  justify-content: flex-end;
  margin-right: 26px;
