  min-height: 196px;
  margin: 0;
  padding: 0;
}
.software__header {
  border-top: 1px solid #D9FFF5;
  width: 320px;
  height: 80px;
  margin-top: 24px;
  background: linear-gradient(270deg, #F8F8F8 0%, #FFFFFF 98.61%);
}
@media (min-width: 768px) {
  .software__header {
    width: 768px;
    height: 56px;
    margin-top: 32px;
  }
}
.software__text {
  margin: 0;
  padding: 16px;
  font-weight: 400;
  font-size: 16px;
  letter-spacing: -0.2px;
  line-height: 24px;
  color: #7E7E82;
}
@media (min-width: 768px) {
  .software__text {
    padding-top: 15px;
    padding-left: 24px;
  }
}

.swiper-slide {
  width: 240px;
}
.swiper-picture {
  margin-left: 16px;
}
.swiper-picture__image {
  width: 108px;
  height: 32px;
}
.swiper__button {
  margin-right: 16px;
}
.swiper-pagination {
  position: static !important;
  margin-top: 16px;
}

.bradns-card {
  display: flex;
  width: 238px;
  height: 70px;
  border-radius: 6px;
  border: 1px solid #EAEAEA;
  background-color: #FFFFFF;
  margin-top: 16px;
  margin-left: 16px;
  align-items: center;
  justify-content: space-between;
}

@media (min-width: 768px) {
  .swiper__container {
    display: none;
  }
